import { Fragment, lazy, useState, Suspense } from 'react'
import { Row, Col, ButtonGroup, Button } from 'reactstrap'
// import Breadcrumbs from '@components/breadcrumbs'
import { Users, Dribbble } from 'react-feather'

const StatisticsCards = () => {
  const [selectedStat, setSelectedStat] = useState(0)

  const Content =
    selectedStat === 0
      ? lazy(() => import('./userReport/list'))
      : selectedStat === 1
      ? lazy(() => import('./referralReport/list'))
      : lazy(() => import('./RedeemReport/list'))
     

  return (
    <Fragment>
      {/* <Breadcrumbs breadCrumbTitle='Statistics Cards' breadCrumbParent='Card' breadCrumbActive='Statistics Cards' /> */}
      <Row>
        <Col md={12} sm={12}>
          <ButtonGroup className="mb-1">
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(0)}
              active={selectedStat === 0}
            >
              <Users size={14} />
              <span className="align-middle ml-25">UserReport</span>
            </Button>
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(1)}
              active={selectedStat === 1}
            >
              <Dribbble size={14} />
              <span className="align-middle ml-25">ReferralReport</span>
            </Button>
            <Button
              outline
              color="primary"
              onClick={() => setSelectedStat(2)}
              active={selectedStat === 2}
            >
              <Dribbble size={14} />
              <span className="align-middle ml-25">RedeemReport</span>
            </Button>
   
          </ButtonGroup>
        </Col>
      </Row>
      <Suspense>
        <Content />
      </Suspense>
    </Fragment>
  )
}

export default StatisticsCards
